import { StandarApprovalService, standarFlow } from 'src/app/components/approval/standar/standar-approval.service';
import { Purchase_request_item } from 'src/app/sdk/asset/models/purchase-_request-_item';
import { Purchase_request } from 'src/app/sdk/asset/models';
import { GaPurchaseRequestService, GaPurchaseRequestItemService, AssetModelxService } from 'src/app/sdk/asset/services';
import { Component, OnInit } from '@angular/core';
import { DirektoriPegawaiMasterService, CompanyKantorService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Pegawai_master, Kantor } from 'src/app/sdk/hr/models';
import { ToolsLogFlowService } from 'src/app/sdk/user/services';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private gaPurchaseRequestService: GaPurchaseRequestService,
        private gaPurchaseRequestItemService: GaPurchaseRequestItemService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private companyKantorService: CompanyKantorService,
        private toolsLogFlowService: ToolsLogFlowService,
        private assetModelxService: AssetModelxService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private standarApprovalService: StandarApprovalService,
    ) { }

    dataRequest: any = {}
    list_item: Purchase_request_item[] = []
    id_purchase_request: string = ''

    dataPegawai: Pegawai_master = {}
    dataKantor: Kantor = {}

    ngOnInit() {
        this.activatedRoute.paramMap.subscribe(
            params => {
                console.log(params.get('id_purchase_request'))
                if (params.get('id_purchase_request')) {
                    this.id_purchase_request = params.get('id_purchase_request')
                }
                this.getDetailRequest()
                this.getDetailItem()
                this.getLog()

            }
        )


        this.standarApprovalService.isreload.next(false);
        this.standarApprovalService.getisreload().subscribe(
            data => {
                if (data) {
                    this.getDetailRequest()
                    this.getDetailItem()
                    this.getLog()
                }
            }
        )


    }
    params: standarFlow = null;
    id_data_flow: string = ''
    getDetailRequest() {
        this.spinner.show()
        this.gaPurchaseRequestService.getGaPurchaseRequestsId(this.id_purchase_request).subscribe(
            data => {
                this.dataRequest = data.data;
                this.getPegawai(data.data['pegawai_request']);
                this.getkantor(data.data['kantor_request']);
                this.spinner.hide()


                this.params = {
                    id: data.data.id_purchase_request,
                    id_data_flow_detail: data.data.id_data_flow_detail,
                    id_data_flow: data.data.id_data_flow,
                    next_status: null,
                    primary_key: "id_purchase_request"
                }
                this.standarApprovalService.param.next(this.params)
            }
        )
    }

    getPegawai(pegawai_request) {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMastersId(pegawai_request).subscribe(
            data => {
                this.dataPegawai = data.data;
            }
        )
    }
    getkantor(kantor_request) {
        this.companyKantorService.getCompanyKantorsId(kantor_request).subscribe(
            data => {
                this.dataKantor = data.data;
            }
        )
    }

    getDetailItem() {
        this.gaPurchaseRequestItemService.getGaPurchaseRequestItems(JSON.stringify({ id_purchase_request: this.id_purchase_request })).subscribe(
            res => {
                this.list_item = res.data;
                this.spinner.hide()
            }
        )
    }

    listLog = [];
    getLog() {
        this.spinner.show()
        this.toolsLogFlowService.getToolsLogFlows(JSON.stringify({ for_module_id: this.id_purchase_request })).subscribe(
            data => {
                this.listLog = data.data;
                this.spinner.hide()
            }
        )
    }

    goToBack() {
        this.router.navigate(['/ga/asset/module/request/list'])
    }

}
