import { NgModule } from '@angular/core'
import { Routes, RouterModule } from '@angular/router'
import { RequestComponent } from './request.component'

const routes: Routes = [
    {
        path: '',
        redirectTo: 'module/biaya',
    },
    {
        path: 'module',
        component: RequestComponent,
        children: [
            {
                path: 'biaya',
                loadChildren: () => import('./biaya/biaya.module').then(mod => mod.BiayaModule),
            },
            {
                path: 'hotel',
                loadChildren: () => import('./hotel/hotel.module').then(mod => mod.HotelModule),
            },
            {
                path: 'pengaturan/untuk',
                loadChildren: () =>
                    import('./pengaturan/untuk/untuk.module').then(mod => mod.UntukModule),
            },
            {
                path: 'pengaturan/uniform',
                loadChildren: () =>
                    import('./pengaturan/uniform/uniform.module').then(mod => mod.UniformModule),
            },
            {
                path: 'tiket',
                loadChildren: () => import('./tiket/tiket.module').then(mod => mod.TiketModule),
            },
            {
                path: 'transport',
                loadChildren: () =>
                    import('./transport/transport.module').then(mod => mod.TransportModule),
            },
            {
                path: 'uniform',
                loadChildren: () =>
                    import('./uniform/uniform.module').then(mod => mod.UniformModule),
            },
        ],
    },
]

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule],
})
export class RequestRoutingModule { }
